module.exports = ".movie-list {\n  text-align: center; }\n\n.list-container {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, 200px);\n  grid-gap: 10px;\n  grid-auto-rows: 250px; }\n\n@media screen and (max-width: 1040px) {\n    .list-container {\n      grid-template-columns: repeat(auto-fill, 32%); } }\n\n@media screen and (max-width: 638px) {\n    .list-container {\n      grid-template-columns: repeat(auto-fill, 48%); } }\n\n@media screen and (max-width: 425px) {\n    .list-container {\n      grid-template-columns: repeat(auto-fill, 100%); } }\n\n.search-movies {\n  height: 50px;\n  margin: 10px 0;\n  width: 300px;\n  padding-left: 10px;\n  font-size: 20px;\n  font-weight: bold;\n  border-radius: 10px;\n  border: 0;\n  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);\n  outline: none;\n  transition: all .4s; }\n\n.search-movies:focus {\n  width: 400px;\n  box-shadow: inset 0 0 0 7px #0397D6; }\n\n.storys {\n  overflow: auto;\n  width: 100%; }\n\n/*# sourceMappingURL=data:application/json;base64, */"